.edgt-events-list {
	text-align: left;
}

.edgt-events-list-item {
	margin-bottom: 30px;

    &.edgt-events-light-date-skin{
        .edgt-events-list-item-date-holder{
            background-color: #fff;

            .edgt-events-list-item-date-day {
                color: #000;
            }

            .edgt-events-list-item-date-month {
                color: #000;
            }
        }

        .edgt-events-list-item-image-holder {
            &:hover {
                .edgt-events-list-item-date-holder{
                    background-color: $first-main-color;
                    color: #fff;
                }
            }
        }
    }
}

.edgt-events-list-item-image-holder {
	position: relative;
	border: 1px solid #f2f2f2;
	margin-bottom: 26px;
	overflow: hidden;

	img {
		display: block;
		vertical-align: middle;
		@include edgtTransform(translateZ(0));
		@include edgtTransition(transform .5s ease);
		backface-visibility: hidden;
	}

	&:hover {
		img {
			@include edgtTransform(scale(1.02));
		}
        .edgt-events-list-item-date-holder{
            background-color: #212121;
        }
	}
}

.edgt-events-list-item-date-holder {
	position: absolute;
	top: 0;
	left: 0;
	color: #fff;
	background-color: $first-main-color;
	width: 70px;
	height: 70px;
	text-align: center;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
    @include edgtTransition(background-color .25s ease-in-out);

	.edgt-events-list-item-date-inner {
		position: relative;
		top: 50%;

		@include edgtTransform(translateY(-50%));
	}

	.edgt-events-list-item-date-day {
		display: block;
        color: #fff;
		margin: 0;
        line-height: 1;
	}

	.edgt-events-list-item-date-month {
        margin: 0;
        color: #fff;
        line-height: 1;
	}
}

.edgt-events-list-item-title-holder {
	position: relative;

	.edgt-events-list-item-title {
		margin-top: 0;
		margin-bottom: 0;
	}
}

.edgt-events-list-item-info {
	margin-top: 15px;
	padding-top: 19px;
	border-top: 1px solid #e1e1e1;
	font-weight: 400;
	color: #808080;
	font-size: 14px;

	.edgt-events-list-item-date {
		margin-bottom: 4px;

        .tribe-event-date-start{
            font-size: 15px
        }
	}

	.edgt-events-item-info-icon {
        padding-right: 3px;
		width: 22px;
		display: inline-block;
        text-align: center;
	}

    .qpdef-events-list-item-location{
        font-size: 15px
    }
}