.edgt-events-list {
    @include ipad-landscape{
        .edgt-events-list-item.edgt-grid-col-4{
            width: 50%;
            float: left;
        }
    }

    @include phone-landscape{
        .edgt-events-list-item.edgt-grid-col-4{
            width: 100%;
            float: none;
        }
    }
}
